<!--看板展示数据-->
<template>
  <div
    class="kbLabel-style"
    v-loading="!kbRes"
    element-loading-background="rgba(0, 0, 0, 0.2)"
    element-loading-text="加载中"
  >
    <div :class="type==='4'?'kb-table3':'kb-table'" v-for="(v,k,i) in dataInfo" :key="i">
      <span class="title">{{k}}</span>
      <span class="value">{{v === null ? 0:v}}</span>
      <!--                <td class="value">{{v === null ? 0:parseInt(v).toLocaleString()}}</td>-->
    </div>
  </div>
</template>

<script>
import Interface from '../../../base/mixin/service-index-control';
import qs from 'qs';

export default {
  name: "kbLabel",
  mixins: [Interface],
  props: {
    type: {
      type: String,
      default: '2'
    }
  },
  data() {
    return {
      dataInfo: {},
      kbRes: false
    }
  },
  watch: {},
  created() {
    if (this.type === '3') {
      this.getTab1Data()
    } else if (this.type === '2') {
      this.getTab2Data();
    } else if (this.type === '1') {
      this.getTab3Data();
    }
  },
  mounted() {
  },
  methods: {
    //业务处理-汇总看板
    getTab1Data() {
      this.getBusinessForSpectaculars(qs.stringify({
        dimensionality: this.$parent.dateType,
        timeStr: this.$parent.dateParams
      })).then(res => {
        this.kbRes = true;
        this.dataInfo = res;
      }).catch(() => {
        this.kbRes = true;
      })
    },
    //派单-汇总看板
    getTab2Data() {
      this.getSendOrdersSpectaculars(qs.stringify({
        dimensionality: this.$parent.dateType,
        timeStr: this.$parent.dateParams
      })).then(res => {
        this.kbRes = true;
        for (let key in res) {
          if (key == '月业务量') {
            // res[key] = res[key] + '件';
            res[key] = res[key]+'';
          }
        }
        this.dataInfo = res;
        // window.console.log(this.dataInfo,'this.dataInfo999999999999999')
      }).catch(() => {
        this.kbRes = true;
      })
    },
    //
    getTab3Data() {
      this.getAckOrdersSpectaculars(qs.stringify({
        dimensionality: this.$parent.dateType,
        timeStr: this.$parent.dateParams
      })).then(res => {
        this.kbRes = true;
        this.dataInfo = res;
      }).catch(() => {
        this.kbRes = true;
      })
    }
  }
}
</script>

<style scoped lang="scss">
.kbLabel-style {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  .kb-table3,
  .kb-table {
    display: flex;
    width: 50%;
    justify-content: center;
    align-items: center;
    //  table-layout: fixed;
    background: url("../../../assets/img/指标框短.png") no-repeat top center;
    background-size: 100% 100%;
    padding: 0px 40px;
    height: 33%;
    box-sizing: border-box;
    span {
      // width: 50%;
    }

    .title {
      width: 70%;
      text-align: left;
      font-size: 18px;
      color: #fff;
      // padding: 0px 5px;
    }

    .value {
      width: 30%;
      text-align: right;
      color: #00e7ff;
      font-size: 20px;
      font-weight: 600;
      // padding: 0px 10px;
    }
  }
  .kb-table {
    &:nth-child(2n + 1) {
      .value {
        color: #00ffd4;
      }
    }
    &:nth-child(2n + 2) {
      .value {
        color: #86ff80;
      }
    }
    &:nth-child(2) {
      width: 100%;
      padding: 0px 40px;
      background: url("../../../assets/img/指标框长.png") no-repeat top center;
      background-size: 100% 100%;
      .value {
        color: #00e7ff;
      }
    }
  }
}
</style>